body {
    font-family: $Helvetica;
    background-color: $body-black;
}

.body_lock{
    overflow: hidden;
}

.container{
    width: 100%;
    max-width: 1440px;
    padding: 0 15px;
    margin: 0 auto;
}

.bottom-line{
    position: relative;

    &::after{
        content:'';
        position: absolute;
        width: 470px;
        height: 4px;
        bottom: 0;
        left: 50%;
        transform: translate(-50%);
        background-color: $main-color;
        z-index: 10;

        @include media('<tablet'){
            width: 250px;
        }
    }
}

.text-bg{
    position: absolute;
    top: 45%;
    font-weight: bold;
    font-size: 96px;
    text-transform: uppercase;
    color: #FFFFFF;
    opacity: 0.2;
    transform: rotate(-90deg);
    margin: 0;
    white-space: nowrap;

    &:nth-child(1){
        left: -360px;
    }

    &:nth-child(2){
        right: -360px;
    }
}

.main-btn{
    display: inline-block;
    min-width: 190px;
    padding: 9px 10px;
    background-color: $main-color;
    border-radius: 50px;
    text-align: center;
    font-size: 20px;
    box-shadow: inset 0px 2px 2px rgba(0, 0, 0, 0.25);
    background-image: linear-gradient($hover-color, $hover-color);
    background-position: 50% 50%;
    background-repeat: no-repeat;
    background-size: 0% 100%;
    box-sizing: border-box;
    transition: all .5s ease 0s;

    &:hover{
        background-size: 100% 100%;
        transition: all .5s ease 0s;
    }
}

/* --------------------------main-section-------------------- */

.main-section{
    position: relative;
    min-height: 100vh;
    padding: 100px 0 60px 0;

    &::before{
        content:'';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: $black;
        opacity: 70%;
        z-index: 2;
    }
}

.main-video{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 1;
}

.main-content{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: calc(100vh - 160px);
    padding: 20px 0 0 0;
    text-align: center;
    z-index: 4;

    h1{
        font-family: 'Bebas Neue', cursive;
        @include adaptiv-vaule("font-size", 320, 75, 1);
        text-shadow: 0px 4px 30px rgba(0, 0, 0, 0.25);
        text-transform: uppercase;
        line-height: 0.85;
        background: #565656;
        background: -webkit-linear-gradient(to bottom, #565656 35%, #393939 60%);
        background: -moz-linear-gradient(to bottom, #565656 35%, #393939 60%);
        background: linear-gradient(to bottom, #565656 35%, #393939 60%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        margin: 0;
    }

    p{
        position: relative;
        @include adaptiv-vaule("font-size", 20, 7, 1);
        font-weight: bold;
        letter-spacing: 0.685em;
        text-transform: uppercase;
        color: #5B5B5B;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        margin: 0 0 60px 0;

        &::before, &::after{
            content:'';
            position: absolute;
            top: 5px;
            height: 7px;
            @include adaptiv-vaule("width", 300, 25, 1);
            background: #5B5B5B;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

            @include media ("<tablet"){
                height: 3px;
            }

            @include media ("<phone"){
                height: 1.5px;
                top: 4px;
            }
        }

        &::before {
            left: 0;
        }

        &::after {
            right: 0;
        }
    }
}

.icon-mouse{
    width: 25px;
    height: 67px;
    margin: 20px auto;

}

/* --------------------------services-section-------------------- */

.services {
    position: relative;
    @include adaptiv-vaule("padding-top", 140, 95, 1);
    @include adaptiv-vaule("padding-bottom", 90 , 50, 1);
    overflow: hidden;
}

.services-content {
    position: relative;
    text-align: center;
    z-index: 2;

    h2 {
        text-align: center;
        margin-bottom: 90px;
        color: #FFFFFF; 
    }
}

.services__item-wrap {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-bottom: 70px;
}

.services__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-width: 370px; 
    max-width: 370px;
    margin-bottom: 70px;
    padding: 0 15px;

    @include media ("<phone") {
        min-width: 100%; 
        max-width: 100%;
    }
}

.services__item-img {
    margin-bottom: 10px;
    text-align: center;
}

.services__item-text {
    text-align: center;
    font-size: 24px;
    color: #FFFFFF;

    span {
        display: block;
        margin-bottom: 5px;
    }

    P {
        font-size: 18px;
    }
}

/* --------------------------about-section-------------------- */

.about {
    @include adaptiv-vaule("padding-top", 140, 70, 1);
    padding-bottom: 5px;
    overflow: hidden;

    h2 {
        text-align: center;
        color: $white;
        @include adaptiv-vaule("margin-bottom", 100, 70, 1);
    }
}

.about-content {
    position: relative;
    padding: 50px 0;
    min-height: calc(100vh - 270px);

    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.about__text-wrap {
    display: flex;
    align-items: center;

    @include media ("<tablet"){
        padding: 0;
        min-height: calc(100vh - 270px);
    }
}

.indefinite {
    width: 100%;
    max-width: 50%;
    min-height: calc(100vh - 270px);

    @include media ("<tablet") {
        display: none;
    }
}

.about__text {
    position: relative;
    max-width: 50%;
    padding: 40px 15px 50px 70px;
    z-index: 2;

    @include media ("<tablet") {
        max-width: 100%;
        padding: 40px 15px;
    }
    
    &::after {
        content:'';
        position: absolute;
        top: 0;
        left: 0;
        width: 1000%;
        height: 100%;
        background-color: $main-color;
        z-index: -1;

        @include media ("<tablet") {
            width: 100%;
        }
    }

    h2 {
        color: $black;
        margin-bottom: 25px;
    }

    p {
        font-size: 18px;
        line-height: 1.4;
    }
}

/* --------------------------project-section-------------------- */

.project {
    position: relative;
    @include adaptiv-vaule("padding-top", 140, 95, 1);
    @include adaptiv-vaule("padding-bottom", 90 , 50, 1);

    .container {
        padding: 0;
    }

    h2 {
        color: $white;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 90px;
    }
}

/* /////////////////////////projects-block///////////////////////// */

.projects {
    display: flex;
    flex-wrap: wrap;
}

.projects__item {
    width: 100%;
    max-width: 33.3%;
    min-width: 33.3%;
    margin-bottom: 90px;

    @include media ("<widescreen") {
        max-width: 50%;
        min-width: 50%;
    }

    @include media ("<tablet") {
        max-width: 100%;
        min-width: 100%;
    }
}

.projects__img {
    position: relative;
    width: 100%;
    min-height: 320px;
    max-height: 320px;
    overflow: hidden;
    margin-bottom: 15px;

    img{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

}

.projects__text {
    display: flex;
    justify-content: space-between;
    padding: 0 15px;
}

.project__text-title {
    padding-right: 15px;

    h2{
        text-align: left;
        @include adaptiv-vaule("font-size", 24, 20, 1);
        font-weight: bold;
        text-transform: none;
        color: $main-color;
        margin-bottom: 10px;
    }

    p{
        @include adaptiv-vaule("font-size", 18, 16, 1);
        line-height: 1.4;
        color: $white;
        margin: 0;
    }
}

.project__text-icon {
    position: relative;
    max-width: 48px;
    min-width: 48px;
    max-height: 48px;
    min-height: 48px;

    svg{
        width: 25px;
        height: 22px;
        fill: $main-color;
        transition: all .4s ease 0s;
    }

    a{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        padding: 10px 5px 10px 10px;
        border: 2px solid $main-color;
        border-radius: 100%;
        box-sizing: border-box;
        transition: all .3s ease 0s;

        &:hover{
            border: 2px solid $hover-color;
    
            svg{
                fill: $hover-color;
                transform: rotate3d(1, 0, 0, 180deg);
                transition: all .4s ease 0s;
            }
        }
    }
    
}